            </div>
            <div class="jackpot-title">本期奖池(元)</div>
            <div class="jackpot-number font-medium" v-text="data.current_jackpot"></div>
            <div class="jackpot-info">
              <span class="have-bet"><span class="text-opacity">已下注数</span> <span class="font-regular" v-text="data.bets"></span></span>
              <i class="segment"></i>
              <span><span class="text-opacity">下期奖池</span> <span class="font-regular" v-text="data.next_jackpot"></span></span>
