            <i class="segment"></i>
            <div class="list-md list-md-2 w86"><span class="text-opacity">期号</span> <span v-text="item.lottery_id"></span></div>
            <div class="list-md list-md-3 w86"><span class="text-opacity">奖池</span> <span v-text="item.jackpot"></span>元</div>
          </div>
          <ul>
            <li class="lottery-list-box" :class="{ lotteryIcon : item.win }" v-for="(item, index) in numbersLotteryList" :key="index">
              <div class="list-md list-md-1">
                <div class="lottery-number">开奖号码<span class="lottery-digital font-medium" v-text="item.totalLotteryNumbers"></span></div>
                <div class="dice">
                  <img class="dice-icon" :src="lotteryIcons[item.lottery_number[0] - 1]"/>
                  <img class="dice-icon" :src="lotteryIcons[item.lottery_number[1] - 1]"/>
                  <img class="dice-icon" :src="lotteryIcons[item.lottery_number[2] - 1]"/>
                </div>
              </div>
              <i class="segment"></i>
              <div class="list-md list-md-2">
                <div class="letter-spacing w86"><span class="text-opacity">期号</span> <span v-text="item.lottery_id"></span></div>
                <div class="w86"><span class="text-opacity">中奖</span> <span v-text="item.reward_bets"></span>注</div>
              </div>
              <div class="list-md list-md-3">
                <div class="letter-spacing w86"><span class="text-opacity">奖池</span> <span v-text="item.jackpot"></span>元</div>
                <div class="winning w86" v-if="item.win"><span>中奖</span> <span v-text="item.my_reward"></span>元</div>
                <div class="w86" v-else><span class="text-opacity">单注</span> <span v-text="item.average_reward"></span>元</div>
              </div>
              <span class="lotteryIconText font-regular" :class="{ hide : !item.win }">中奖</span>
            </li>
          </ul>
        </section>
        <div v-if="gotData" slot="infinite" class="text-center" v-text="''"></div>
        <div v-if="noMoreData" slot="infinite" class="text-center" v-text="'没有更多数据'"></div>
      </scroll>
    </div>
    <footer class="lottery-index-footer">
      <a class="lottery-index-btn obtain-chip font-regular" @click="$router.forward('/lottery/getChips')">获取筹码</a>
      <a class="lottery-index-btn immediately-bet font-regular" @click="$router.forward('/lottery/betting')">立即下注</a>
    </footer>
  </div>
