      width: 100%;
      height: 346px;
      background: linear-gradient(-1deg, #23193A 8%, #4E3A76 100%);

      .daily-lottery {
        display: block;
        width: 100%;
        height: 88px;
      }

      .my-chips {
        height: 40px;
        line-height: 37px;
        width: 303px;
        margin: 10px auto 14px;
        background: #873C96;
        box-shadow: inset 0 -4px 0 0 rgba(0, 0, 0, 0.1);
        border-radius: 4px;
        color: white;
        font-size: 14px;

        .amount,
        .get-chips,
        .chips-image {
          display: inline-block;
          vertical-align: middle;
        }

        .amount {
          span {
            margin: 0 2px;
          }

          display: inline-block;
