        margin: 0 auto 0;
        text-align: center;
        overflow: hidden;
        background: url("//qianka.b0.upaiyun.com/images/d459b7e542347b34e6af3849f346b31f.png") center no-repeat;
        background-size: 100% 100%;

        .jackpot-nper {
          position: relative;
          display: inline-block;
          height: 21px;
          line-height: 22px;
          margin: 20px auto 0;
          padding: 0 10px;
          border-radius: 50px;
          font-size: 12px;
          opacity: 0.7;
          background: rgba(255, 255, 255, 0.3);

          .w52 {
            display: inline-block;
            width: 50px;
          }

          .time-deadline {
            position: absolute;
            margin-left: -50px;
          }
        }

        .jackpot-title {
          font-size: 13px;
          margin-top: 20px;
          margin-bottom: 6px;
        }

        .jackpot-number {
          height: 32px;
          line-height: 32px;
          color: #106ACC;
          font-size: 32px;
        }

        .jackpot-info {
          margin-top: 20px;
          font-size: 12px;

          .text-opacity {
            opacity: 0.7;
          }

          .segment {
            display: inline-block;
            width: 0;
            height: 11px;
            margin: -1.5px 10px 0;
            vertical-align: middle;
            border: 0.5px solid #44AEF2;
            transform: scaleX(0.5);
