  }

  .scroll {
    top: -1px;
    bottom: 55px;

    &.no-loading {
      .infinite-layer {
        display: none !important;
      }
    }
  }

  .lottery-index-footer {
    position: absolute;
    width: 100%;
    height: 56px;
    bottom: 0;
    background: #342755;
    z-index: 100;

    .lottery-index-btn {
      float: left;
      color: #FFF;
      width: 164px;
      height: 40px;
      line-height: 40px;
      text-align: center;
      border-radius: 2px;
      margin-top: 8px;
