</style>
<script>
  import CountDown from '../widget/CountDown'

  const PAGE_SIZE = 10

  export default {
    components: {
      CountDown
    },
    data () {
      return {
        data: {},
        openLotteryList: [],
        numbersLotteryList: [],
        fetchingData: false,
        noMoreData: false,
        gotData: false,
        offset: '',
        end_time: 0,
        // start_time: 0,
        lotteryIcons: [ // 骰子图片地址
          '//qianka.b0.upaiyun.com/images/dd25ce1de98fb43669e3f7fed9587c74.jpeg',
          '//qianka.b0.upaiyun.com/images/f33109b3cfb04b7c5918fc8f80c6c69f.jpeg',
          '//qianka.b0.upaiyun.com/images/ed150ebf159cf744fbd344d26a970f29.jpeg',
          '//qianka.b0.upaiyun.com/images/b9c1d3d1ed1d4bb7a95d856ad84d8e7f.jpeg',
          '//qianka.b0.upaiyun.com/images/e3323978ae8d156abe4b8ae8d8a29d05.jpeg',
          '//qianka.b0.upaiyun.com/images/56902d33c7a67c13d42dbf444abe3db3.jpeg'
        ]
      }
    },

    mounted () {
      this.fetchData()
    },

    methods: {
      fetchData () {
        if (this.loadingTimer) {
          clearTimeout(this.loadingTimer)
          $loading.hide()
        }

        this.loadingTimer = setTimeout(_ => {  // show loading indicator only for slow requests/network. more than 0.2 sec.
          $loading.show('拼命加载中..')
        }, 300)

        $api.lotteryDashboard()
        .then(res => res.data && res.data.payload)
        .then((payload) => {
          this.gotData = true // flag for list spinner incase of network error

          if (this.loadingTimer) {
            clearTimeout(this.loadingTimer)
            setTimeout(_ => {
              $loading.hide()
            }, 500)
          }

          this.data = payload
          this.end_time = moment(this.data.end_time, 'YYYY-MM-DD hh:mm:ss').valueOf() // 用moment方法转化时间格式为时间戳
          // this.start_time = moment(this.data.start_time, 'YYYY-MM-DD hh:mm:ss').valueOf()
          this.processHistory(payload.history, false)
        }, () => {
          this.gotData = true
        })
      },

      infinite (done) {
        if (!this.offset || this.noMoreData || this.fetchingData) {
          done()
          return
        }

        this.fetchingData = true

        this.getHistory(done)
      },

      getHistory (done) {
        $api.lotteryDashboardHistory(this.offset)
          .then((res) => {
            let payload = res.data ? res.data.payload : undefined

            setTimeout(_ => {
              done()
              this.processHistory(payload, true)
            }, 1000)
          }, () => {
            done()
          })
      },

      processHistory (history, afterscroll) {
        if (afterscroll && (history.length < PAGE_SIZE)) {
          this.noMoreData = true
        }

        if (history.length <= 0) {
          return
        }

        this.fetchingData = false

        this.offset = history[history.length - 1].lottery_id
        let tmpList = _.filter(history, {lottery_status: 1}) // 分离出lottery_status为1的数据 1为开奖中
        let tmpNumbersList = _.filter(history, {lottery_status: 2}) // 分离出lottery_status为2的数据 2为已开奖

        this.openLotteryList = this.openLotteryList.concat(tmpList)
        this.numbersLotteryList = this.numbersLotteryList.concat(tmpNumbersList)
        _.each(tmpNumbersList, (item, index) => { // 算出开奖号码之和
          item.totalLotteryNumbers = _.sum([item.lottery_number[0], item.lottery_number[1], item.lottery_number[2]])
        })
      },

      countdownComplete () {
        window.location.reload()
      }
    }
  }
</script>



